<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
      <el-button type="primary">添加角色</el-button>
      <!-- 表格区域 -->
      <el-table :data="rolesList" style="width: 100%" stripe>
        <!--展开列 -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row v-for="item1 in scope.row.children" :key="item1.id" class="level_one">
              <!-- 一级 -->
              <el-col :span="6">
                <el-tag>{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- 二级跟三级一起 -->
              <el-col :span="18">
                <el-row v-for="item2 in item1.children" :key="item2.id" class="level_two">
                  <!-- 二级 -->
                  <el-col :span="6">
                    <el-tag type="success">{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!-- 三级 -->
                  <el-col :span="18">
                    <el-tag
                      type="danger"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      closable
                      @close="removeRightById(scope.row,item3.id)"
                    >{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!-- 索引列 -->
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="roleName" label="角色名称"></el-table-column>
        <el-table-column prop="roleDesc" label="角色描述"></el-table-column>
        <el-table-column prop="address" label="操作">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini">编辑</el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini">删除</el-button>
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="showSettingDiglog(scope.row)"
            >分配权限</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 分配权限对话框 -->
    <el-dialog title="分配权限" :visible.sync="settingDialogVisible" width="50%">
      <!-- 树形控件 -->
      <el-tree
        :data="rightsList"
        :props="treeProps"
        default-expand-all
        show-checkbox
        :default-checked-keys="defkeys"
        node-key="id"
        ref="allocateRightsRef"
      ></el-tree>

      <!-- 底部 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="settingDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="allocateRights">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      rolesList: [],
      // 显示分配权限对话框
      settingDialogVisible: false,
      // 权限列表数据
      rightsList: [],
      // 树形控件
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      // 权限id数组
      defkeys: [],
      roleId: ''// 角色id
    }
  },
  methods: {
    //   获取角色列表
    async getRolesList () {
      const { data: res } = await this.$http.get('roles')
      // console.log(res);
      if (res.meta.status != 200) { return this.$message.error('获取角色列表失败') }
      this.rolesList = res.data
    },
    // 通过id删除权限
    removeRightById (role, rightId) {
      // 确认删除
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          // 确认了删除
          let { data: res } = await this.$http.delete(
            `roles/${role.id}/rights/${rightId}`
          )
          if (res.meta.status != 200) { return this.$message.error('删除权限失败') }
          // 重新赋值
          role.children = res.data
          // 删除成功
          this.$message.success('删除权限成功')
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    // 显示分配权限对话框
    async showSettingDiglog (role) {
      this.roleId = role.id // 给角色id赋值
      var keys = []// 用来保存最后一级权限的id
      // 第一种方式
      // role.children.forEach(item1=>{
      //   // 一级权限
      //   if(item1.children){//判断为了防止报错
      //     item1.children.forEach(item2=>{
      //     // 二级权限
      //       if(item2.children){
      //         item2.children.forEach(item3=>{
      //           // 三级权限
      //           keys.push(item3.id)
      //         })
      //       }
      //     })
      //   }
      // })
      // 第二种方式  递归函数
      function getId (obj) {
        obj.children.forEach(item => {
          if (item.children) {
            // 递归: 自己调用自己 递归一定要有结束条件
            getId(item)
          } else {
            keys.push(item.id)
          }
        })
      }
      getId(role)
      this.defkeys = keys
      //  发送请求获取权限列表数据
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status != 200) { return this.$message.error('获取权限列表失败') }
      // 成功
      this.rightsList = res.data
      this.settingDialogVisible = true
    },
    // 实现分配权限的功能
    async allocateRights () {
      // console.log(this.$refs.allocateRightsRef.getHalfCheckedKeys())
      // console.log(this.$refs.allocateRightsRef.getCheckedKeys())
      var keys = [
        ...this.$refs.allocateRightsRef.getHalfCheckedKeys(),
        ...this.$refs.allocateRightsRef.getCheckedKeys()
      ]
      var keysStr = keys.join(',')
      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, {
        rids: keysStr
      })
      if (res.meta.status != 200) return this.$message.error('分配权限失败')
      // 重新获取角色列表
      this.getRolesList()
      this.settingDialogVisible = false
    }
  },
  created () {
    this.getRolesList()
  }
}
</script>
<style scoped lang="less">
.el-tag {
  margin: 10px;
}
.level_one {
  border-bottom: 1px solid #ccc;
}
.level_two {
  border-bottom: 1px solid #ccc;
}
.level_two:last-child {
  border-bottom: none;
}
</style>
